.loader {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 3px dotted black;
    animation: rotate 1s infinite alternate-reverse;
}

@keyframes rotate {
    from {
        transform: rotate(0deg) scale(1);
    }

    to {
        transform: rotate(360deg) scale(1.3)
    }
}